
import { Injectable } from '@angular/core';
import { Plugins } from '@capacitor/core';
import { environment as env } from 'src/environments/environment';
const { Storage } = Plugins;

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  constructor() { }

  async validarAuth(): Promise<boolean> {
    let data = await this.get(env.APP_KEY);
    console.log('data_validarAuth',data);
    if (data) return Promise.resolve(true);
    return Promise.resolve(false);
  }

/*   async get(name): Promise<any> {
    let data = JSON.parse(localStorage.getItem(name));
    return Promise.resolve(data);
  } */

 /*  async set(key, value) {
    localStorage.setItem(key, JSON.stringify(value));
    return Promise.resolve(true);
  } */

 /*   remove() {
    localStorage.clear();
  } */


  async get(name) {
    let item = await Storage.get({ key: name }) || null;
    if (!item) return null;
    return JSON.parse(item.value);
  }

  async set(name, data) {
    await Storage.set({ key: name, value: JSON.stringify(data) });
  }

/*   async remove(name){
    await Storage.remove({ key: name });
  } */


  async remove() {
    await Storage.clear();
  }

}
