import { Injectable } from '@angular/core';
import { BehaviorSubject, of } from 'rxjs';
import { Router } from '@angular/router';
import { environment as env } from 'src/environments/environment';
import { Funciones } from '../compartido/funciones';
import { StorageService } from './storage.service';
import { UserService } from './user.service';

export interface User {
  name: string;
  role: string;
  permissions: string[];
}

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  public currentUser: BehaviorSubject<any> = new BehaviorSubject(null);
  public userData: any = null;
  routesPages = [];

  constructor(
    private _user: UserService,
    private _storage: StorageService,
    private _fun: Funciones,
    private router: Router) {
  }

  loadUser() {
    return new Promise(async (resolve, reject) => {
      try {
        let keyUser = await this._storage.get(env.APP_KEY);
        console.log('RESPUESTA DE LOADUSER', keyUser);
        if (keyUser) {
          const res_u: any = await this._user.get(keyUser);
          if(!res_u.valid){
            this.currentUser.next(false);
            resolve(false);
            this.router.navigate(['/login']);
          }

          this.userData = res_u.res;
          this.currentUser.next(res_u.res);
          resolve(res_u.res);
        } else {
          this.currentUser.next(false);
          resolve(false);
          this.router.navigate(['/login']);
        }

      } catch (error) {
        await this._fun.alertError(error);
      }
    });
  }

  // Access the current user
  getUser() {
    return this.currentUser.asObservable();
  }

  /*   async validarAuth(): Promise<boolean> {
      return new Promise((resolve, reject) => {
        this.getUser().subscribe(res => {
        
          if (res){
            console.log('SE DIERON CAMBIOS', res);
            resolve(res);
          } 
          resolve(false);
        });
  
      });
    } */

  async validarAuth(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      let user: any = this._storage.get(env.APP_KEY) || false;
      resolve(user);
    });
  }

  // Remove all information of the previous user
  async logout() {
    let alert = await this._fun.alertGen('Si, cerrar','Esta seguro de cerrar sesión');
    if (this._fun.isVarInvalid(alert)) return;
    this._storage.remove();
    this.currentUser.next(false);
    this.router.navigate(['/login']);
  }

  // Check if a user has a certain permission
  hasPermission(permissions: string[]): boolean {
    for (const permission of permissions) {
      if (!this.currentUser.value || !this.currentUser.value.permissions.includes(permission)) {
        return false;
      }
    }
    return true;
  }
}